.benefits {
	&__list {
		margin: 58px 0 45px;
		font-size: 0;
		text-align: center;
		@include sm {
			margin: 40px 0; }
		@include xs {
			margin: 25px 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 20px 45px;
		text-align: center;
		width: 33.33333%;
		@include sm {
			padding: 0 10px 20px; }
		@include xs {
			width: 50%; }
		@include r(599) {
			width: 100%;
			padding: 0 10px 10px; }
		&-pic {
			height: 90px;
			@include vertical;
			@include sm {
				margin-bottom: 4px; }
			&::after {
				content: '';
				display: inline-block;
				vertical-align: middle; }
			&_tape::after {
				margin-bottom: 5px;
				@include s('tape'); }
			&_peoples::after {
				@include s('peoples'); }
			&_building::after {
				@include s('building'); }
			&_vehicle::after {
				margin-bottom: 10px;
				vertical-align: bottom;
				@include s('vehicle'); }
			&_handshake::after {
				margin-bottom: 5px;
				@include s('handshake'); }
			&_pc-shield::after {
				@include s('pc-shield'); }
			&_marker::after {
				@include s('marker'); }
			&_pen-ruler::after {
				@include s('pen-ruler'); }
			&_present::after {
				@include s('present'); }
			&_list::after {
				@include s('list'); }
			&_box::after {
				@include s('box'); }
			&_push-btn::after {
				margin-bottom: 5px;
				@include s('push-btn'); }
			&_cards::after {
				@include s('cards'); }
			&_equipment::after {
				@include s('equipment'); }
			&_pen-list::after {
				@include s('pen-list'); }
			&_types::after {
				@include s('types'); }
			&_paper::after {
				@include s('paper'); } }
		&-text {
			margin: 5px 0 0; }

		&_horizontal {
			text-align: left;
			@include clr;
			@include md {
				padding: 0 7px 25px; }
			@include sm {
				width: 50%; }
			@include xs {
				width: 100%; } }
		&_horizontal &-pic {
			float: left;
			width: 86px;
			margin-right: 14px;
			@include md {
				margin-right: 5px; } }
		&_horizontal &-text {
			float: left;
			width: calc(100% - 100px);
			@include md {
				width: calc(100% - 92px); } }

		&-title {
			margin-bottom: 5px;
			font-size: 22px;
			font-weight: 700;
			line-height: 1;
			color: $black;
			@include sm {
				font-size: 19px; }
			sup {
				vertical-align: super;
				font-size: 15px; } }
		&-sub-title {
			margin-bottom: 5px;
			font-size: 16px;
			font-weight: 500;
			color: $grey; }
		&-descr {
			font-size: 16px;
			color: $grey;
			@include sm {
				font-size: 15px; }
			p {
				@include md {
					display: inline; } } } }
	&__slider {
		@include r(1219) {
			max-width: 840px;
			margin: 0 auto; }
		@include sm {
			max-width: 600px; }
		@include xs {
			max-width: 400px;
			padding: 0 20px; }
		&-item {
			height: 132px; }
		.owl-carousel__btn {
			@include vertical;
			&_prev {
				left: -40px;
				@include xs {
					left: -5px; } }
			&_next {
				right: -40px;
				@include xs {
					right: -5px; } }
			&-icon {
				display: inline-block;
				vertical-align: middle;
				stroke: $grey;
				fill: none;
				font-size: 44px; } } }

	&_default {
		padding: 65px 0 45px;
		@include sm {
			padding: 50px 0 10px; } }
	&_souvenirs {
		padding: 65px 0 63px;
		@include sm {
			padding: 45px 0 10px; } }
	&_polygraph {
		padding: 65px 0 63px;
		@include sm {
			padding: 45px 0 10px; } }
	&_polygraph &__item-descr {
		max-width: 260px;
		margin: 0 auto; }

	&_big {
		padding: 65px 0 125px;
		@include sm {
			padding: 50px 0 70px; }
		@include xs {
			padding: 40px 0 50px; } }
	&_big &__list {
		@include md {
			margin: 40px 0 15px; } }
	&_about {
		padding: 65px 0 20px; }
	&_about &__list {
		margin: 13px 0 15px; }
	&_about &__item {
		padding: 0 20px 35px; } }

