.articles {
	padding: 45px 0 60px;
	@include sm {
		padding: 40px 0; }
	@include xs {
		padding: 20px 0; }
	&__container {
		display: flex;
		justify-content: space-between;
		@include xs {
		  flex-wrap: wrap; } }
	&__col {
		flex: 0 1 825px;
		margin-left: 15px;
		@include xs {
			margin: 20px 0 0; } } }
.article {
	&__title {
		text-align: left; }
	&__in {
		margin: 25px 0 28px;
		@include clr;
		p {
			line-height: 1.2;
			@include xs {
				font-size: 15px; }
			&:not(:last-child) {
				margin-bottom: 20px;
				@include xs {
					margin-bottom: 10px; } } } }
	&__date {
		@include xs {
			margin: 5px 0 0;
			font-size: 14px; } }
	&__img-wrap {
		margin-right: 30px;
		float: left;
		width: percentage(540 / 825);
		@include sm {
			width: 55%;
			margin-right: 15px; } } }
