.work {
	&__list {
		display: flex;
		justify-content: space-between;
		margin: 27px 0 0;
		@include sm {
			justify-content: space-around;
			flex-wrap: wrap; }
		@include xs {
			justify-content: center;
			margin: 30px -8px 0; }
		@include r(424) {
			margin: 30px 0 0; } }
	&__card {
		position: relative;
		flex: 0 1 200px;
		margin-bottom: 20px;
		overflow: hidden;
		@include sm {
			margin: 0 15px 30px; }
		@include xs {
			margin: 0 4px 15px; }
		@include r(424) {
			flex-grow: 1;
			max-width: 320px; }
		&:not(:last-child) {
			margin-right: 27px;
			@include md {
				margin-right: 10px; }
			@include sm {
				margin: 0 15px 30px; }
			@include xs {
				margin: 0 4px 15px; } }
		&-title {
			position: absolute;
			top: 6%;
			left: 12%;
			right: 8%;
			font-size: 15px;
			color: $white;
			font-weight: 700;
			line-height: 22px;
			@include md {
				left: 6%; }
			@include sm {
				font-size: 14px; }
			@include r(424) {
				font-size: 24px;
				line-height: 1.25;
				top: 3%;
				left: 4%; } }
		&-count {
			position: absolute;
			bottom: -14px;
			right: 5%;
			font-size: 100px;
			line-height: 1;
			color: rgba($white, .2);
			font-weight: 900;
			@include r(424) {
				font-size: 130px;
				bottom: 0; } }
		&-bg-wrap {
			padding-top: 200%; }
		&-bg {
			@include coverdiv;
			@include cover;
			z-index: -1; } }
	&_default {
		padding: 70px 0 67px;
		@include sm {
			padding: 45px 0 50px; } } }
