.category {
	position: relative;
	&::after {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		content: '';
		display: block;
		background: url('../img/bg/sect-bg.jpg') repeat-x 0 0;
		height: 12px; }
	&__in {
		margin: 66px 0 0;
		@include clr;
		@include xs {
			display: flex;
			flex-direction: column;
			margin: 35px 0 0; }
		&::after {
			@include xs {
				display: none; } } }
	&__list {
		float: left;
		margin-right: (30 / 1110 * 100%);
		width: percentage(350 / 1110);
		@include sm {
			margin-right: 2.7%; }
		@include xs {
			order: 2;
			float: none;
			width: 100%;
			margin: 20px 0 0; }
		&-title {
			padding: 0 0 16px;
			color: $black;
			font-size: 22px;
			line-height: 1;
			font-weight: 700;
			border-bottom: 1px solid #e6e7e8;
			@include sm {
				padding: 0 0 10px;
				font-size: 20px; } }
		&-links {
			padding: 42px 20px 42px 0;
			@include sm {
				padding: 36px 0; } }
		&-item {
			&:not(:last-child) {
				margin-bottom: 10px; } }
		&-item.slick-current &-text {
			color: $red;
			border-bottom-color: $red; }
		&-text {
			display: inline;
			cursor: pointer;
			border-bottom: 1px solid rgba($grey, .7);
			transition: border-bottom-color .2s, color .2s;
			line-height: 19px;
			@include hover {
				color: $red;
				border-bottom-color: $red; } }
		&-btn {
			position: absolute;
			padding: 5px;
			background: transparent;
			@include rotate(90);
			&.slick-disabled {
				visibility: hidden; }
			&_prev {
				left: 15px;
				top: -10px; }
			&_next {
				bottom: -5px;
				left: 15px; }
			&-icon {
				font-size: 44px;
				stroke: $black; } } }
	&__view {
		float: left;
		width: percentage(730 / 1110);
		@include xs {
			order: 1;
			float: none;
			width: 100%;
			text-align: center; }
		&-item {
			height: 300px;
			overflow: hidden;
			@include r(599) {
				height: 200px; }
			img {
				transition: all 550ms ease-in-out;
				transform: scale(1.25, 1.25);
				opacity: .5; }
			&.slick-active {
				img {
					transform: scale(1, 1);
					opacity: 1; } } }
		&-btn {
			margin-top: 20px;
			margin-left: 57px;
			padding: 0 27px;
			@include sm {
				margin-left: 0; } } }
	&__list-links, &__view-pic {
		display: none;
		&.slick-initialized {
			display: block; } }

	&_default {
		padding: 64px 0 175px;
		@include md {
			padding: 60px 0 130px; }
		@include sm {
			padding: 45px 0 70px; }
		@include xs {
			padding: 40px 0 30px; }
		.next-section {
			bottom: 10%;
			@include md {
				bottom: 5%; } } }
	&_calendars {
		padding: 64px 0 165px;
		&::after {
			display: none; }
		@include md {
			padding: 60px 0 130px; }
		@include sm {
			padding: 45px 0 50px; }
		@include xs {
			padding: 30px 0 0; } } }
